import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import { changeColorToClassName, Colors } from '../../utils/color';
import Label, { LabelTypography, LabelColors } from '../Label';

export default function DateSeparator({
  className,
  children,
  separatorColor,
}) {
  const injectingClassName = (Array.isArray(className)) ? className : [className];
  return (
    <div
      className={[...injectingClassName, 'sendbird-separator'].join(' ')}
    >
      <div className={[`${changeColorToClassName(separatorColor)}--background-color`, 'sendbird-separator__left'].join(' ')} />
      <div className="sendbird-separator__text">{children}</div>
      <div className={[`${changeColorToClassName(separatorColor)}--background-color`, 'sendbird-separator__right'].join(' ')} />
    </div>
  );
}

DateSeparator.propTypes = {
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.element,
  ]),
  separatorColor: PropTypes.string,
};

DateSeparator.defaultProps = {
  className: '',
  children: (
    <Label type={LabelTypography.CAPTION_2} color={LabelColors.ONBACKGROUND_2}>
      Date Separator
    </Label>
  ),
  separatorColor: Colors.ONBACKGROUND_4,
};
