import React from 'react';
import PropTypes from 'prop-types';
import { createPortal } from 'react-dom';

import './index.scss';
import Avatar from '../Avatar/index';
import Label, { LabelTypography, LabelColors } from '../Label';
import Icon, { IconTypes } from '../Icon';
import { MODAL_ROOT } from '../../hooks/useModal/ModalRoot';

import { isImage, isVideo, unSupported } from './types';

export const FileViewerComponent = ({
  profileUrl,
  nickname,
  type,
  url,
  name,
  onClose,
  onDelete,
  isByMe,
}) => (
  <div className="sendbird-fileviewer">
    <div className="sendbird-fileviewer__header">
      <div className="sendbird-fileviewer__header-left">
        <div className="sendbird-fileviewer__header-avatar">
          <Avatar height="32px" width="32px" src={profileUrl} />
        </div>
        <Label
          className="sendbird-fileviewer__header-filename"
          type={LabelTypography.H_2}
          color={LabelColors.ONBACKGROUND_1}
        >
          {name}
        </Label>
        <Label
          className="sendbird-fileviewer__header-sendername"
          type={LabelTypography.BODY_1}
          color={LabelColors.ONBACKGROUND_2}
        >
          {nickname}
        </Label>
      </div>
      <div className="sendbird-fileviewer__header-right">
        {
          !unSupported(type) && (
            <div className="sendbird-fileviewer__header-actions">
              <a
                href={url}
                rel="noopener noreferrer"
                target="_blank"
                className="sendbird-fileviewer__header-download"
              >
                <Icon
                  type={IconTypes.DOWNLOAD}
                  height="24px"
                  width="24px"
                />
              </a>
              {
                onDelete && isByMe && (
                  <div className="sendbird-fileviewer__header-delete">
                    <Icon
                      type={IconTypes.DELETE}
                      height="24px"
                      width="24px"
                      onClick={onDelete}
                    />
                  </div>
                )
              }
            </div>
          )
        }
        <div className="sendbird-fileviewer__header-close">
          <Icon
            type={IconTypes.CLOSE}
            height="24px"
            width="24px"
            onClick={onClose}
          />
        </div>
      </div>
    </div>
    <div className="sendbird-fileviewer__content">
      {isVideo(type) && (
        // eslint-disable-next-line jsx-a11y/media-has-caption
        <video controls className="sendbird-fileviewer__video">
          <source src={url} type={type} />
        </video>
      )}
      {
        isImage(type) && (
          <img
            src={url}
            alt={name}
            className="sendbird-fileviewer__img"
          />
        )
      }
      {
        unSupported(type) && (
          <div className="sendbird-fileviewer__unsupported">
            <Label type={LabelTypography.H_1} color={LabelColors.ONBACKGROUND_1}>
              Unsupoprted message
            </Label>
          </div>
        )
      }
    </div>
  </div>
);

FileViewerComponent.propTypes = {
  profileUrl: PropTypes.string.isRequired,
  nickname: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  isByMe: PropTypes.bool,
};

FileViewerComponent.defaultProps = {
  isByMe: true,
};

export default function FileViewer(props) {
  const {
    onClose,
    message,
    onDelete,
    isByMe,
  } = props;
  const {
    sender,
    type,
    url,
    name = '',
  } = message;
  const { profileUrl, nickname = '' } = sender;
  return createPortal(
    (
      <FileViewerComponent
        profileUrl={profileUrl}
        nickname={nickname}
        type={type}
        url={url}
        name={name}
        onClose={onClose}
        onDelete={onDelete}
        isByMe={isByMe}
      />
    ),
    document.getElementById(MODAL_ROOT),
  );
}

FileViewer.propTypes = {
  onClose: PropTypes.func.isRequired,
  message: PropTypes.shape({
    sender: PropTypes.shape({
      profileUrl: PropTypes.string,
      nickname: PropTypes.string,
    }),
    type: PropTypes.string,
    url: PropTypes.string,
    name: PropTypes.string,
  }).isRequired,
  onDelete: PropTypes.func.isRequired,
  isByMe: PropTypes.bool,
};

FileViewer.defaultProps = {
  isByMe: true,
};
